<template>
  <div class="main-pages">
    <Header/>
    <!--main-container start-->
    <div class="main-container">
      <Side/>
      <!--main-content start-->
      <div class="main-content">
        <el-scrollbar class="content-scroll">
          <div class="main-inner">
            <!--inner-goods start-->
            <div class="inner-goods">
              <!--inner-bread start-->
              <div class="inner-bread">
                <router-link to="">我的商品</router-link>
                <span class="icon"><img src="../../assets/images/cart4.png"></span>
                <span>新增商品</span>
              </div>
              <!--inner-bread end-->
              <!--add-goods start-->
              <div class="add-goods">
                <!--add-goods-item start-->
                <div class="add-goods-item">
                  <div class="label">分类：</div>
                  <div class="add-goods-col">
                    <div class="add-goods-radio" @click="tabLogin(1)">
                      <input type="radio" name="1" checked />
                      <span>新机</span>
                    </div>
                    <div class="add-goods-radio" @click="tabLogin(2)">
                      <input type="radio" name="1" />
                      <span>二手</span>
                    </div>
                  </div>
                </div>
                <!--add-goods-item end-->
                <div class="add-goods-first" v-if="loginType == 1">
                  <!--add-goods-item start-->
                  <div class="add-goods-item">
                    <div class="label">商品分类：</div>
                    <div class="goods-select">
                      <select class="select">
                        <option>选择分类</option>
                      </select>
                    </div>
                    <div class="goods-select">
                      <select class="select">
                        <option>选择品牌</option>
                      </select>
                    </div>
                    <div class="goods-select">
                      <select class="select">
                        <option>选择系列</option>
                      </select>
                    </div>
                    <div class="goods-select">
                      <select class="select">
                        <option>选择配置和颜色</option>
                      </select>
                    </div>
                  </div>
                  <!--add-goods-item end-->
                  <!--add-goods-item start-->
                  <div class="add-goods-item">
                    <div class="label">销售价格：</div>
                    <div class="add-goods-input mar-r2">
                      <input class="input" type="text" />
                      <span class="s-tip">元</span>
                    </div>
                    <div class="label">商品排序</div>
                    <div class="add-goods-input mar-l5">
                      <input class="input" type="text" />
                    </div>
                  </div>
                  <!--add-goods-item end-->
                  <!--add-goods-item start-->
                  <div class="add-goods-item">
                    <div class="label">商品库存：</div>
                    <div class="add-goods-input">
                      <input class="input" type="text" value="1000" />
                    </div>
                    <div class="add-goods-select mar-l15">
                      <select class="select">
                        <option>无货</option>
                      </select>
                    </div>
                  </div>
                  <!--add-goods-item end-->
                  <!--add-goods-item start-->
                  <div class="add-goods-item">
                    <div class="label">商品标签：</div>
                    <div class="add-goods-tags">
                      <input type="checkbox" />
                      <span>商品标签1</span>
                    </div>
                    <div class="add-goods-tags">
                      <input type="checkbox" />
                      <span>商品标签1</span>
                    </div>
                    <div class="add-goods-tags">
                      <input type="checkbox" />
                      <span>商品标签1</span>
                    </div>
                    <div class="add-goods-tags">
                      <input type="checkbox" checked />
                      <span>商品标签1</span>
                    </div>
                    <div class="add-goods-tags">
                      <input type="checkbox" checked />
                      <span>商品标签1</span>
                    </div>
                  </div>
                  <!--add-goods-item end-->
                  <div class="add-goods-bot">
                    <button class="add-goods-btn">提交发布</button>
                  </div>
                </div>
                <div class="add-goods-second" v-if="loginType == 2">
                  <!--add-goods-item start-->
                  <div class="add-goods-item">
                    <div class="label">商品分类：</div>
                    <div class="goods-select">
                      <select class="select">
                        <option>选择分类</option>
                      </select>
                    </div>
                    <div class="goods-select">
                      <select class="select">
                        <option>选择品牌</option>
                      </select>
                    </div>
                    <div class="goods-select">
                      <select class="select">
                        <option>选择系列</option>
                      </select>
                    </div>
                    <div class="goods-select">
                      <select class="select">
                        <option>选择配置和颜色</option>
                      </select>
                    </div>
                  </div>
                  <!--add-goods-item end-->
                  <!--add-goods-item start-->
                  <div class="add-goods-item">
                    <div class="label">销售价格：</div>
                    <div class="add-goods-input mar-r2">
                      <input class="input" type="text" />
                      <span class="s-tip">元</span>
                    </div>
                    <div class="label">商品排序</div>
                    <div class="add-goods-input mar-l5">
                      <input class="input" type="text" />
                    </div>
                  </div>
                  <!--add-goods-item end-->
                  <!--add-goods-item start-->
                  <div class="add-goods-item">
                    <div class="label">商品库存：</div>
                    <div class="add-goods-input">
                      <input class="input" type="text" value="1000" />
                    </div>
                    <div class="add-goods-select mar-l15">
                      <select class="select">
                        <option>无货</option>
                      </select>
                    </div>
                  </div>
                  <!--add-goods-item end-->
                  <!--add-goods-item start-->
                  <div class="add-goods-item">
                    <div class="label">商品标签：</div>
                    <div class="add-goods-tags">
                      <input type="checkbox" />
                      <span>商品标签1</span>
                    </div>
                    <div class="add-goods-tags">
                      <input type="checkbox" />
                      <span>商品标签1</span>
                    </div>
                    <div class="add-goods-tags">
                      <input type="checkbox" />
                      <span>商品标签1</span>
                    </div>
                    <div class="add-goods-tags">
                      <input type="checkbox" checked />
                      <span>商品标签1</span>
                    </div>
                    <div class="add-goods-tags">
                      <input type="checkbox" checked />
                      <span>商品标签1</span>
                    </div>
                  </div>
                  <!--add-goods-item end-->
                  <!--add-goods-item start-->
                  <div class="add-goods-item">
                    <div class="add-goods-con">
                      <div class="label">商家资质 <span class="color-red">*</span></div>
                      <div class="add-goods-up">
                        <el-upload
                          action="https://jsonplaceholder.typicode.com/posts/"
                          list-type="picture-card"
                          :on-preview="handlePictureCardPreview"
                          :on-remove="handleRemove">
                          <i class="el-icon-plus"></i>
                        </el-upload>
                      </div>
                    </div>
                  </div>
                  <!--add-goods-item end-->
                  <!--add-goods-item start-->
                  <div class="add-goods-item">
                    <div class="label">验机报告  <span class="color-red">*</span></div>
                    <div class="add-goods-add">
                      <button class="add-goods-add-btn orange">+添加正常项</button>
                      <button class="add-goods-add-btn">+添加瑕疵项</button>
                    </div>
                  </div>
                  <!--add-goods-item end-->
                  <!--add-goods-item start-->
                  <div class="add-goods-item">
                    <div class="add-goods-con">
                      <div class="label">瑕疵项展示</div>
                      <div class="add-goods-bor">
                        <div class="add-goods-bor-input">
                          <input type="text" placeholder="请输入" />
                        </div>
                        <div class="add-goods-bor-up">
                          <el-upload
                            action="https://jsonplaceholder.typicode.com/posts/"
                            list-type="picture-card"
                            :on-preview="handlePictureCardPreview"
                            :on-remove="handleRemove">
                            <i class="el-icon-plus"></i>
                          </el-upload>
                        </div>
                      </div>
                    </div>
                  </div>
                  <!--add-goods-item end-->
                  <!--add-goods-item start-->
                  <div class="add-goods-item">
                    <div class="add-goods-con">
                      <div class="label">正常项展示</div>
                      <div class="add-goods-bor">
                        <div class="add-goods-bor-label">屏幕有问题</div>
                        <div class="add-goods-bor-up">
                          <el-upload
                            action="https://jsonplaceholder.typicode.com/posts/"
                            list-type="picture-card"
                            :on-preview="handlePictureCardPreview"
                            :on-remove="handleRemove">
                            <i class="el-icon-plus"></i>
                          </el-upload>
                        </div>
                      </div>
                    </div>
                  </div>
                  <!--add-goods-item end-->
                  <div class="add-goods-bot">
                    <button class="add-goods-btn">提交发布</button>
                  </div>
                </div>
              </div>
              <!--add-goods end-->
            </div>
            <!--inner-goods end-->
          </div>
        </el-scrollbar>
      </div>
      <!--main-content end-->
    </div>
    <!--main-container end-->
  </div>
</template>

<script>
import Side from '@/components/side'
import Header from '@/components/header'
export default {
  data() {
    return {
      loginType: 1,
      dialogImageUrl: '',
      dialogVisible: false
    };
  },
  components:{
    Side,
    Header
  },
  methods: {
    //切换新机二手
    tabLogin: function (index) {
      this.loginType = index;
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    }
  }
}
</script>

<style scoped>
@import "../../assets/styles/goods.less";
</style>



